import React, { useState } from "react";
import { ToastContainer, toast } from "react-toastify";
import axios from "axios";

import "./contact.css";

const Contact = () => {
  const [form, setForm] = useState({
    name: "",
    email: "",
    subject: "",
    message: "",
  });

  // const [status, setStatus] = useState("");

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    // setStatus("  ...");

    try {
      const res = await toast.promise(
        // 1.      (   )
        axios.post("http://localhost:4000/api/contact", { ...form }),

        // 2.     
        {
          pending: "    ...", //        
          success: "     .", //        
          error: {
            //        
            render({ data }) {
              //      
              return (
                data.response?.data?.message || "     ."
              );
            },
          },
        }
      );

      // await axios.post("http://localhost:4000/api/contact", form);
      // setStatus("      .");
      setForm({ name: "", email: "", subject: "", message: "" });
    } catch (err) {
      console.error(err);
      // setStatus("      .");
    }
  };

  return (
    <section className="contact container section" id="contact">
      <h2 className="section__title">
             <span className="title__emoji"></span>
      </h2>

      <div className="contact__container grid">
        <div className="contact__info">
          <h3 className="contact__title">       !</h3>
          <p className="contact__details">   .</p>
        </div>

        <form action="" className="contact__form" onSubmit={handleSubmit}>
          <div className="contact__form-group">
            <div className="contact__form-div">
              <input
                type="text"
                name="name"
                className="contact__form-input"
                placeholder="    ..."
                value={form.name}
                onChange={handleChange}
              />
            </div>

            <div className="contact__form-div">
              <input
                type="email"
                name="email"
                className="contact__form-input"
                placeholder=" ..."
                value={form.email}
                onChange={handleChange}
                required
              />
            </div>
          </div>

          <div className="contact__form-div">
            <input
              type="text"
              name="subject"
              className="contact__form-input"
              placeholder=" ..."
              value={form.subject}
              onChange={handleChange}
              required
            />
          </div>

          <div className="contact__form-div">
            <textarea
              id=""
              cols="30"
              rows="10"
              type="email"
              name="message"
              className="contact__form-input textarea"
              placeholder=" ..."
              value={form.message}
              onChange={handleChange}
              required
            />
          </div>

          <button type="submit" className="btn">
             
          </button>
        </form>

        <ToastContainer
          position="top-center" // :   
          autoClose={5000} //       
          rtl={true} //     
          theme="colored" //    
        />
        {/* {status && <p className="status">{status}</p>} */}
      </div>
    </section>
  );
};

export default Contact;
